.Content{height:100vh;position:fixed;left:0;top:0;display:flex;color:#fff;opacity:0;opacity:1;transition:3s cubic-bezier(0.61, 0.01, 0.3, 1);z-index:2}.Content section{width:100vw;height:100vh;position:relative}.Content section>article{height:100%;box-sizing:border-box;position:absolute;top:0;left:0;overflow:hidden}.Content section>article.bigL{background:#022e32;width:40vw;transform:translate3d(20vw, 0, 0);padding:5.5vw}.Content section>article.smallL{width:35vw;transform:translate3d(20vw, 0, 0)}.Content section>article.smallL>div{transition:3s cubic-bezier(0.61, 0.01, 0.3, 1);background:#022e32;padding:10vh 6vw;margin-top:10vh}.Content section>article.smallL>div p{font-size:1.3em;margin:0 0 30px 0}.Content section>article.smallR{width:35vw;transform:translate3d(50vw, 0, 0)}.Content section>article.smallR>div{transition:3s cubic-bezier(0.61, 0.01, 0.3, 1);background:#022e32;padding:10vh 6vw;margin-top:10vh}.Content section>article.smallR>div p{font-size:1.3em;margin:0 0 30px 0}.Content section>article.smallFL{width:35vw;transform:translate3d(2vw, 0, 0)}.Content section>article.smallFL>div{transition:3s cubic-bezier(0.61, 0.01, 0.3, 1);background:#022e32;padding:10vh 6vw;margin-top:10vh}.Content section>article.smallFL>div p{font-size:1.3em;margin:0 0 30px 0}.Content section>article.smallRTop{transform:translate3d(50vw, 0, 0);width:25vw}.Content section>article.smallRTop>div{transition:3s cubic-bezier(0.61, 0.01, 0.3, 1);background:#022e32;padding:7vh 6vw;margin-top:10vh}.Content section>article.smallRTop>div p{font-size:1.3em;margin:0 0 30px 0}.Content section>article.smallRTop.long{width:40vw}.Content section>article.smallRBottom{display:flex;align-items:flex-end;transform:translate3d(50vw, 0, 0);width:35vw}.Content section>article.smallRBottom>div{transition:3s cubic-bezier(0.61, 0.01, 0.3, 1);background:#022e32;padding:7vh 6vw;margin-bottom:15vh}.Content section>article.smallRBottom>div p{font-size:1.3em;margin:0 0 30px 0}.Content section>article h2{transition:3s cubic-bezier(0.61, 0.01, 0.3, 1);font-size:5.8em;line-height:1.2em;margin:0;font-family:'SegoeBold','Arial','Sans-serif'}.Content .email{display:flex;flex-direction:column;font-size:2.5em;margin-top:80px;transition:3s cubic-bezier(0.61, 0.01, 0.3, 1)}.Content .email strong{font-family:'SegoeBold','Arial','Sans-serif';font-size:2.5em;line-height:1em}.Content .email span{line-height:1em}.Content .source{position:absolute;bottom:20px;right:80px;color:#fff}.Content .calendar{background:url("../../assets/icons/calendar.svg") no-repeat 25px center #022e32;background-size:auto 45px;position:absolute;bottom:20px;left:0;color:#fff;display:inline-flex;padding:20px 40px 20px 100px;font-size:2em;transition:3s cubic-bezier(0.61, 0.01, 0.3, 1)}
