@keyframes grow{from{transform:scale(1);opacity:1}to{transform:scale(2);opacity:.3}}h1.m{width:100vw;height:100vh;position:fixed;top:0;left:0;margin:0;color:#000;font-size:10em;text-align:center;display:flex;align-items:center;justify-content:center;z-index:2}.Anims>div{width:100vw;height:100vh;position:fixed;top:0;left:0;z-index:1}.Anims>div.triangles{transition:1s ease}.Anims>div.triangles.radar{transition:1s cubic-bezier(0.73, -0.01, 0.25, 1);transform:scale(0.25) translate3d(0, 0, 0)}.Anims>div.triangles.radar.bigRadar{transform:scale(1.3) translate3d(15vw, 0, 0)}.Anims>div.triangles.big{animation:grow ease 1s forwards 1.5s}.Anims .showTriangle{transition:.5s ease;opacity:0;transform:scale(0);width:100vw;height:100vh;position:fixed;top:0;left:0}.Anims .showTriangle.show{opacity:1;transform:scale(1)}.Anims .padlock{opacity:.7;transition:6s ease}.Anims .darkweb{opacity:1;transition:.5s ease}.Anims .darkweb.out{opacity:0}
