.Nav{position:fixed;z-index:3;top:0;bottom:0;right:0;display:flex}.Nav ul{display:flex;height:100%;list-style:none;flex-direction:column;padding:0;margin:0;justify-content:space-between}.Nav ul li{display:flex;align-items:center;height:100%;padding:0 20px;position:relative;cursor:pointer}.Nav ul li.selected span,.Nav ul li:hover span{transform:scale(1.2);background:#fff;border:1px solid rgba(255,255,255,0)}.Nav ul li.selected span a,.Nav ul li:hover span a{transform:translate3d(7px, 0, 0);opacity:1}.Nav ul span{transform:scale(1);text-decoration:none;transition:.3s ease;display:block;border:1px solid #79e4d3;width:5px;height:5px;border-radius:50%}.Nav ul span a{position:absolute;right:20px;top:-5px;color:#fff;font-size:.7em;transition:.4s ease;transform:translate3d(20px, 0, 0);opacity:0;text-decoration:none}
